"use client";

import { motion } from "motion/react";
import { useEffect, useRef, useState } from "react";
import { ArrowRight } from "@untitledui/icons";

export const ThreatFacts = () => {
    const ref = useRef<HTMLElement | null>(null);
    const [seen, setSeen] = useState(false);
    const [selected, setSelected] = useState(0);

    const items = [
        {
            title: "Attacks are continuous",
            body: "Threat activity runs at all hours — not isolated events.",
        },
        {
            title: "Automated and repeated",
            body: "Most threats are scripted and will try again and again.",
        },
        {
            title: "Missed alerts cause damage",
            body: "Risk rises when signals are ignored or lost in noise.",
        },
    ];

    useEffect(() => {
        const el = ref.current;
        if (!el) return;
        const io = new IntersectionObserver(
            (entries) => {
                if (entries[0].isIntersecting) setSeen(true);
            },
            { threshold: 0.25 },
        );
        io.observe(el);
        return () => io.disconnect();
    }, []);

    return (
        <motion.section
            ref={ref}
            initial={{ opacity: 0, y: 12 }}
            animate={seen ? { opacity: 1, y: 0 } : { opacity: 0, y: 12 }}
            transition={{ duration: 0.6, ease: "easeOut", delay: 0.15 }}
            className="relative w-full bg-[#0A0D12] overflow-hidden py-16 md:py-24"
        >
            <div className="pointer-events-none absolute inset-0">
                <div
                    className="absolute inset-0 opacity-90"
                    style={{
                        background:
                            "radial-gradient(120% 120% at 50% 50%, rgba(0,0,0,0) 60%, rgba(0,0,0,0.5) 100%)",
                    }}
                />
                <div
                    className="absolute left-0 top-0 h-[55vh] w-[55vw] blur-3xl opacity-35"
                    style={{
                        background:
                            "radial-gradient(circle at 30% 30%, rgba(158,119,237,0.28) 0%, rgba(127,86,217,0.14) 35%, rgba(127,86,217,0) 65%)",
                    }}
                />
                <div
                    className="absolute right-0 bottom-8 h-[55vh] w-[55vw] blur-3xl opacity-30"
                    style={{
                        background:
                            "radial-gradient(circle at 70% 70%, rgba(127,86,217,0.24) 0%, rgba(105,65,198,0.12) 35%, rgba(105,65,198,0) 65%)",
                    }}
                />
                <div
                    className="absolute inset-0 opacity-20"
                    style={{
                        backgroundImage:
                            "linear-gradient(to right, rgba(255,255,255,0.06) 1px, transparent 1px), linear-gradient(to bottom, rgba(255,255,255,0.06) 1px, transparent 1px)",
                        backgroundSize: "80px 80px, 80px 80px",
                        backgroundPosition: "center",
                    }}
                />
            </div>
            <div className="mx-auto w-full max-w-container px-4 md:px-8">
                <div className="grid grid-cols-1 gap-10 md:grid-cols-12">
                    <motion.div
                        initial={{ opacity: 0, x: -16 }}
                        animate={seen ? { opacity: 1, x: 0 } : { opacity: 0, x: -16 }}
                        transition={{ duration: 0.6, ease: "easeOut" }}
                        className="md:col-span-6"
                    >
                        <div className="inline-flex items-center rounded-full p-[1px] ring-1 ring-white/10">
                            <span className="flex items-center gap-2 rounded-full bg-[#0A0D12]/80 px-3 py-1 text-md font-medium text-white/90 ring-1 ring-white/10 backdrop-blur-sm">
                                Modern Threats
                            </span>
                        </div>
                        <h2 className="mt-6 text-3xl font-semibold text-white md:text-4xl">
                            Modern cyber threats are persistent, not occasional
                        </h2>
                        <ul className="mt-8 flex flex-col gap-6">
                            {items.map((item, idx) => (
                                <li key={item.title}>
                                    <button
                                        type="button"
                                        onMouseEnter={() => setSelected(idx)}
                                        onFocus={() => setSelected(idx)}
                                        onClick={() => setSelected(idx)}
                                        className="group w-full text-left"
                                    >
                                        <div className="flex items-start gap-4">
                                            <div className="mt-1 h-full min-h-14 w-0.5 rounded-full bg-brand-500/40 group-hover:bg-brand-400 group-focus:bg-brand-400" />
                                            <div className="flex-1">
                                                <h3 className="text-lg font-semibold text-white">{item.title}</h3>
                                                <p className="mt-1 max-w-md text-white/70">{item.body}</p>
                                                <div className="mt-3 inline-flex items-center gap-2 text-brand-300">
                                                    <span className="text-sm font-medium">Learn more</span>
                                                    <ArrowRight className="size-4" />
                                                </div>
                                            </div>
                                        </div>
                                    </button>
                                </li>
                            ))}
                        </ul>
                    </motion.div>
                    <motion.div
                        initial={{ opacity: 0, x: 16 }}
                        animate={seen ? { opacity: 1, x: 0 } : { opacity: 0, x: 16 }}
                        transition={{ duration: 0.6, ease: "easeOut", delay: 0.1 }}
                        className="md:col-span-6"
                    >
                        <div className="relative rounded-xl md:rounded-2xl bg-[#0B0D12] ring-1 ring-white/10 shadow-2xl overflow-hidden">
                            <div
                                className="absolute inset-0 opacity-20"
                                style={{
                                    backgroundImage:
                                        "linear-gradient(to right, rgba(255,255,255,0.06) 1px, transparent 1px), linear-gradient(to bottom, rgba(255,255,255,0.06) 1px, transparent 1px)",
                                    backgroundSize: "80px 80px, 80px 80px",
                                    backgroundPosition: "center",
                                }}
                            />
                            <div className="relative z-10 grid h-[360px] place-items-center">
                                <div className="relative w-[86%] max-w-xl">
                                    {[
                                        "linear-gradient(135deg, rgba(168,135,255,0.85), rgba(127,86,217,0.75))",
                                        "linear-gradient(135deg, rgba(94,234,212,0.8), rgba(34,197,94,0.65))",
                                        "linear-gradient(135deg, rgba(255,183,77,0.85), rgba(255,92,92,0.75))",
                                    ].map((bg, i) => (
                                        <motion.div
                                            key={i}
                                            initial={false}
                                            animate={{
                                                y: i === 0 ? 0 : i === 1 ? 24 : 48,
                                                scale: i === 0 ? 1 : i === 1 ? 0.97 : 0.94,
                                                opacity: i === 0 ? 1 : 0.9,
                                                filter: "drop-shadow(0 12px 24px rgba(0,0,0,0.35))",
                                            }}
                                            transition={{ type: "spring", stiffness: 200, damping: 24 }}
                                            className="absolute inset-x-0 top-0 mx-auto h-44 rounded-2xl ring-1 ring-white/20"
                                            style={{
                                                background:
                                                    i === 0
                                                        ? [
                                                              "linear-gradient(135deg, rgba(168,135,255,0.85), rgba(127,86,217,0.75))",
                                                              "linear-gradient(135deg, rgba(94,234,212,0.8), rgba(34,197,94,0.65))",
                                                              "linear-gradient(135deg, rgba(255,183,77,0.85), rgba(255,92,92,0.75))",
                                                          ][selected]
                                                        : bg,
                                            }}
                                        />
                                    ))}
                                </div>
                            </div>
                        </div>
                    </motion.div>
                </div>
            </div>
        </motion.section>
    );
};
